div{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: center;
    flex-wrap: wrap;
}


body{
  /* margin-left: 0px;  */
  background-color: black;
}
h1{
  display: flex;
  color: white;
  justify-content: center;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.image-container p{
      text-align: center;
      color: cornflowerblue;
      margin-top: -37px;
      margin-bottom: 1rem;
   }


form{
  width: 540px;
  display: flex;
  justify-content: center;
  border: 2px solid rgb(234, 232, 232);
  padding: 3px;
  color: black;
  border-radius: 10px;
}
.image-container ul{
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 500px;
  width: inherit;
}
.image-container img{
  margin: 50px;
  padding: 15px;

  border: 1px solid grey;
}
button{
  background-color: white;
  border-radius: 0 0.5em 0.5em 0;
  width: 7rem;
}
App{
  display: flex;
  justify-content: center;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}
input[type="file" i]{
  padding: 5px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

#delete{
  max-width: 250px;
  border-radius: 0;
}

.delete{
  border: none;
  align-content: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
